  series: {
    description:
      "<p>The landscape of contemporary art is ever-changing. It shifts according to countless factors, from artists principles and the political climate to auction records and collectors tastes. Nevertheless, each year, a new crop of ambitious artists stands out. They catapult from obscurity to ubiquity, earn representation from top galleries, garner interest from prominent collectors, and pack their schedules with exhibitions. Most importantly, they make work that expands our understanding of what art can be. </p><p>The Artsy Vanguard 2019 features 50 artists, hailing from 27 countries and working in 27 cities around the world. Ranging in age from 28 to 93, they pursue painting, sculpture, photography, filmmaking, and performance, as well as investigative research and virtual reality. They delve into topics from human rights violations to youth culture, and capture the attention of powerhouse collectors and celebrity royalty, like Beyonc. </p><p><i>Artsy </i>editors developed this list from a pool of 600 artists who were nominated by more than 100 curators, collectors, and art-world professionals. These artists represent three distinct career stages, which weve arranged into the following categories: Emerging, which introduces artists who recently started showing at leading institutions and galleries; Newly Established, which presents the artists making noise at major art events and gaining representation with influential galleries; and Getting Their Due, which recognizes artists who have worked persistently for decades, yet have only recently received the spotlight they deserve. The Artsy Vanguard highlights the artists paving the future of art <i>right now</i>.</p>",
    sub_title: "The Artists To Know Right Now",
  },
