                '<p>Through videos and installations, artist <a href="https://www.artsy.net/artist/monira-al-qadiri">Monira Al Qadiri</a> creates unsettling, supernatural, or kitschy atmospheres. Drawing on history, ritual, culture, and power, she shifts viewers into a new headspace. For her 2017 video installation <em>The Craft</em>commissioned by Gasworks in London and <a href="https://www.artsy.net/sursock">The Sursock Museum</a> in BeirutAl Qadiri turned her childhood memories in Kuwait into a paranoid narrative in which aliens invade from the brightly lit interior of an American diner. Her distrust increasingly blossoms, addressing first her familial relationships, and then society itself.</p>',
