              body:
                '<p>Hammer Museum curator Erin Christovale, who co-curated Made in L.A., remarked that the artists works are rich in texture, personal narratives, and various weaving traditions. Indeed, Brackenss works are seeped in symbolism. They meld together a spectrum of influences from the <a href="https://www.artsy.net/article/artsy-editorial-alabama-women-made-quilts-modern-art">Gees Bend quilters</a> of Alabama to the Unicorn Tapestries that were produced during the turn of the 16th century in Paris, Christovale noted, while also invoking Americas history of slavery through his use of cotton. Both his technique and context work together to highlight a black and queer experience in todays society, she added.</p>',
