              type: "text",
              body:
                '<p>One of Gutierrezs recent works, the magazine <em>Indigenous Woman </em>(2018), was inspired by aspirations to be a cover girl and to behold a glossy fashion magazine that reflected her own ideals, mixing glamorous looks, indigenous garb, and pop culture. <a href="https://www.artsy.net/risdmuseum">RISD Museum</a> curator Dominic Molon included Gutierrez in the show Bona Drag earlier this spring. Molon noted that the artists use of motifs from art history and commercial advertising confuses the boundaries between them, provocatively suggesting how both sell through seduction, whether the product is high-end merchandise or established legacies of power and authority. </p>',
