              body:
                '<p><a href="https://www.artsy.net/artist/genesis-belanger">Genesis Belanger</a> charmed the New York art world in the fall of 2017 with her small-but-mighty show of otherworldly ceramic foodstuffs, cigarettes, and fingers at Mrs. Gallery. Her ceramicswith lush pastel hues, matte surfaces, trompe loeil aesthetics, and finely hewn detailstranscend the typical clay-and-glaze constructions we expect from the medium.</p>',
