                '<p><a href="https://www.artsy.net/artist/jordan-nassar">Jordan Nassar</a> harnesses the disarming charm of embroidery to devastating effect. His handmade works meld dramatic mountainscapes of cascading slopes saturated with color, which are overlaid with abstract patterns sourced from traditional Palestinian embroidery. Initially, Nassar generated the patterns for his work via computer and hand-stitched them himself, but he has since collaborated with female artisans in the West Bank who stitch the patterns; he then overlays the colorful landscapes.</p>',
