              type: "text",
              body:
                '<p>In the years that followed, Bennani staged solo shows at the now-closed (but deeply influential) Brooklyn gallery Signal, as well as one at <a href="https://www.artsy.net/moma-ps1">MoMA PS1</a>. In the words of Jocelyn Miller, the curator of the latter show, Meriems work foregrounds powerfully charismatic women with tenderness and humorone of the most subversive and empathetic art forms we have to complicate the ways we understand ourselves and others.</p>',
