              body:
                '<p><a href="https://www.artsy.net/artist/derek-fordjour">Derek Fordjour</a>s vibrant paintings entice the viewer with colorful surfaces, frequently built up with skillfully applied cardboard and newspaper. They often feature athletes, cheerleaders, and marching bands. Fittingly, last December, the artists sold-out presentation (with <a href="https://www.artsy.net/josh-lilley">Josh Lilley</a>) at Art Basel in Miami Beach was a gravel-covered arena with enamored fairgoers becoming his cheering crowd. </p>',
