              type: "text",
              body:
                '<p>During Art Basel in Miami Beach last December, the art world was drawn to the Rubell Family Collection to see Chases dynamic paintings. The following March, his work was in demand at <a href="https://www.artsy.net/michael-kohn-gallery">Kohn Gallery</a>s booth at <a href="https://www.artsy.net/show/kohn-gallery-kohn-gallery-at-the-armory-show-2019">The Armory Show</a>; the Los Angeles gallery began representing the artist in 2017 (he also shows with Company Gallery in New York). In 2018, the <a href="https://www.artsy.net/lacma">Los Angeles County Museum of Art</a> added a work by Chase to its collection; and during The Armory Shows VIP preview this year, more institutions eagerly acquired his works, including Minneapoliss <a href="https://www.artsy.net/walker-art-center">Walker Art Center</a> and the <a href="https://www.artsy.net/icamiami">Institute of Contemporary Art, Miami</a>. </p>',
