              type: "text",
              body:
                '<p>In March 2018, <a href="https://www.artsy.net/artist/elle-perez/shows">Elle Prez</a>s first show at 47 Canal, titled In Bloom, immediately announced the artist as a new voice in photography, one that could explore the contemporary body with raw intimacy. One of the photos showed a bloody scab- and scar-lined hand resting with popping veins between two legs.</p>',
