              type: "text",
              body:
                '<p>Cathrin Mayer, co-curator of Wangs 2018 show at <a href="https://www.artsy.net/kunste-werke">KW Institute for Contemporary Art</a> in Berlin, remarked on the artists incredible ability to tell stories which make us aware of how the body and identity are always culturally relative. In her daring as well as romantic works, she explores how both are shaped by language, garments, and architecture, and reveals the fragile grounds of the self.</p>',
