              type: "text",
              body:
                '<p>There is something particularly current about <a href="https://www.artsy.net/artist/jill-mulleady">Jill Mulleady</a>s paintings. Her vaguely surrealistic scenes entail figures who get lost in private moments on cell phones, or in substance-induced oblivion while objects take on a sinister quality. An atmospheric tension pervades the scenes, curdling the air. Valrie Knoll, who curated Mulleadys 2017 show at Kunsthalle Bern, noted that the artists paintings lure us into worlds infiltrated by an air of violence and uncanniness. She added, They are intense, sexual, cold, and often nightmarish.</p>',
