                    "<p>Installation view of Lawrence Abu Hamdan, <i>This whole time there were no land mines, </i> 2017, in May You Live In Interesting Times, at the Venice Biennale. Photo by Italo Rondinella. Courtesy of the artist and La Biennale di Venezia and Maureen Paley, London.</p>",
