                '<p>The creative duo that is <a href="https://www.artsy.net/artist/barbara-wagner-and-benjamin-de-burca/shows">Brbara Wagner &amp; Benjamin de Burca</a> got their start only six years ago, when the two collaborated on a photographic series exploring the new middle class in Brazil. They have since developed some 10 comprehensive projects for biennials, exhibitions, and festivals around the world. Their signature works investigate youth culture and marginalized communities through a multidisciplinary practice that integrates filmmaking, anthropology, performance, music, and dance. The artists meld documentary and fiction by working in collaboration with their protagonists: rappers in Toronto; Swingueira dancers in Brazil; <em>schlager</em> singers in Germany.</p>',
