              body:
                '<p>Winner of the <a href="https://www.artsy.net/show/tate-britain-turner-prize-2018">2018 Turner Prize</a>, <a href="https://www.artsy.net/artist/charlotte-prodger">Charlotte Prodger</a> debuted an intimate film at this years Venice Biennale representing Scotland at its satellite pavillion. In this newest work, the British artist juxtaposes breathtaking shots of nature with spoken anecdotes that sound like diary entries about queer relationships and sexual encounters. The viewer must work out the connections between the disembodied voice, personal stories, and the landscape on screen. Brief pauses in the dialogue offer time for additional contemplation and looking. Titled <em>SaF05</em> (2019), the work completes a trilogy that began with <em>Stoneymollan Trail</em> (2015) and <em>BRIDGIT</em> (2016).</p>',
