                '<p>Last fall, Gibson had his first solo show with his new New York gallery, <a href="https://www.artsy.net/sikkema-jenkins-and-co">Sikkema Jenkins &amp; Co.</a>, which showcased large-scale paintings of pointed phrases rendered in stylized text and popping hues, framed with traditional glass beads. The show coincided with two traveling institutional exhibitions of his work, which were collectively shown at six museums across the United States. Additionally, Gibson had a <a href="https://www.artsy.net/show/new-museum-1-jeffrey-gibson-the-anthropophagic-effec">solo exhibition</a> at the <a href="https://www.artsy.net/newmuseum">New Museum</a> this year. He is also represented by <a href="https://www.artsy.net/kavi-gupta-gallery">Kavi Gupta</a> in Chicago and <a href="https://www.artsy.net/roberts-and-tilton">Roberts Projects</a> in Los Angeles.</p><p>The artist has become a fixture on the -ennial circuit, with works in the current <a href="https://www.artsy.net/show/whitney-museum-of-american-art-1-whitney-biennial-2019-1">Whitney Biennial</a>, the 2017 Desert X biennial, SITElines.2016, and the third edition of the Prospect New Orleans triennial. His visually seductive and thematically rich works have been acquired by the <a href="https://www.artsy.net/mfaboston">Museum of Fine Arts, Boston</a>; the <a href="https://www.artsy.net/whitneymuseum">Whitney Museum of American Art</a>; the <a href="https://www.artsy.net/newarkmuseum">Newark Museum</a>; and the <a href="https://www.artsy.net/crystal-bridges">Crystal Bridges Museum of American Art</a>, among others.</p>',
