              type: "text",
              body:
                '<p>What makes Jeffreys work so inspiring is how it creates a safe space to have difficult conversations, and prompts us all to center discourse on historically marginalized communities, said John Lukavic, curator of Native arts at the <a href="https://www.artsy.net/denver-art-museum">Denver Art Museum</a>, who curated the institutions presentation of the traveling show <a href="https://www.artsy.net/show/denver-art-museum-jeffrey-gibson-like-a-hammer">Jeffrey Gibson: Like a Hammer</a> last year. His work is also innovative in the way he seamlessly and respectfully blends Indigenous and Western art histories to create something entirely new.</p>',
