              type: "text",
              body:
                '<p>Hao combines figuration with abstraction; fuses the influences of Chinese tradition with <a href="https://www.artsy.net/artist/wassily-kandinsky">Wassily Kandinsky</a>, <a href="https://www.artsy.net/artist/sergei-eisenstein">Sergei Eisenstein</a>, and Gilles Deleuze; and introduces timeless subject matter to the modern, industrial worldspinning ferris wheels dot classic landscapes to create a soaring sense of cosmic time and space. </p><p>Curator Barbara Pollack noted that Hao has mastered not only the sensitive application of ink, but the multiple perspectives that reside simultaneously in many scroll paintings. She added, He seeks to instill in the modern viewer the sublime sensations that masterpieces of Chinese painting are capable of evoking, while integrating elements of <a href="https://www.artsy.net/gene/surrealism">Surrealism</a> and <a href="https://www.artsy.net/gene/biomorphic">biomorphic</a> abstraction from Western art history.</p>',
