                '<p>Strap on a headset and enter a VR realm of voguing ball dancers, figures in gimp masks, and leather daddies on spaceships. This is the world dreamt up by the artist <a href="https://www.artsy.net/artist/jacolby-satterwhite">Jacolby Satterwhite</a>, who has been on a roll since emerging as one of the stars of the 2014 Whitney Biennial. In 2018, he brought an extension of <em>Blessed Avenue </em>(2018)an installation with a VR component first shown at <a href="https://www.artsy.net/gavin-browns-enterprise">Gavin Browns Enterprise</a> in New Yorkto Art Basel in Basel, Switzerland, for a booth with <a href="https://www.artsy.net/ohwow/overview">Morn Morn</a>. </p>',
