                '<p>The <a href="https://www.artsy.net/article/artsy-editorial-firelei-baezs-intoxicating-installation-feminist-ode-african-diaspora">narrative complexities</a> of history, folklore, and personal experience collide in Dominican-American artist <a href="https://www.artsy.net/artist/firelei-baez-1">Firelei Bez</a>s energetically saturated paintings, sculptures, and installations. Afro-Caribbean women of the diaspora are the heroes of her intoxicating, symbol-laden works. The artist draws upon fiction and historical record in equal measure to reimagine the migration of these women across the globe, as well as the resilient and distinct communities that they helped build.</p>',
