              type: "text",
              body:
                '<p>Bezs insightful and prescient work has quickly earned rapt audiences and institutional acclaim. In recognition of her work about migration, Bez was awarded a 2019 Soros Arts Fellowship. Furthermore, she was picked up by New York gallery <a href="https://www.artsy.net/james-cohan-gallery">James Cohan</a> in October 2018 (Bez also shows with <a href="https://www.artsy.net/kavi-gupta-gallery">Kavi Gupta Gallery</a>); was prominently featured in the 10th Berlin Biennale; and has had recent major solo exhibitions at the <a href="https://www.artsy.net/studio-museum">Studio Museum in Harlem</a>, the <a href="https://www.artsy.net/pamm">Prez Art Museum Miami</a>, <a href="https://www.artsy.net/thewarholmuseum">The </a><a href="https://www.artsy.net/thewarholmuseum">Andy Warhol Museum</a> in Pittsburgh, and the Contemporary Art Center, Cincinnati.</p>',
