              body:
                '<p>The individuals who make up <a href="https://www.artsy.net/artist/forensic-architecture">Forensic Architecture</a> have exposed the lies of the Israeli Defense Forces, Russias foreign ministry, and Syrian president Bashar al-Assad, along with many other powerful people and organizations. The collective, founded by Israeli-British architect <a href="https://www.artsy.net/artist/eyal-weizman">Eyal Weizman</a>, is a multidisciplinary team of architects, designers, developers, archaeologists, filmmakers, and more. Nominated for the Turner Prize in 2018, the group has been known to investigate human rights violations, launch legal battles, and reveal truths through exhibitions, journalism, and the internet.</p>',
