              type: "text",
              body:
                '<p>What makes <a href="https://www.artsy.net/artist/tschabalala-self">Tschabalala Self</a> such a rising star is her visionary approach to textile-based canvases, where the fabrics act as backgrounds to the painted figures. Tschabalala brings the cutting edge of visual culture today into dialogue with a Western art-historical canon that has held as a tradition the exclusion and marginalization of the black figure, said Legacy Russell, associate curator at the <a href="https://www.artsy.net/studio-museum">Studio Museum in Harlem</a>, where Self has been an artist-in-residence from 201819. </p>',
