              body:
                '<p><a href="https://www.artsy.net/artist/harold-ancart">Harold Ancart</a> has a feverish approach to putting out work. Hes produced enough of his large-scale paintingsof matches, icebergs, and other abstracted formsto be in 14 shows in 8 years with his New York gallery, the influential Bushwick- and Brussels-based hub <a href="https://www.artsy.net/c-l-e-a-r-i-n-g">Clearing</a>. He got so restless to paint on a road trip across the United States that he built a makeshift studio in his trunk. And since signing on with mega-gallery <a href="https://www.artsy.net/david-zwirner">David Zwirner</a> in 2018, Ancart has made enough work to fill walls at fairs such as Frieze New York and Los Angeles, Art Basel in Hong Kong, ART021 in Shanghai, and FIAC in Paris. </p>',
