              body:
                '<p><a href="https://www.artsy.net/artist/frank-bowling">Frank Bowling</a> is a master colorist known for his large-scale abstract compositions with evocative details layered between oozing, glowing hues. The artist, now in his mid-eighties, has been long overdue for the type of recognition hes finally getting. After solo shows at Haus der Kunst (curated by the late Okwui Enwezor) and the <a href="https://www.artsy.net/dallasmuseumart">Dallas Museum of Art</a>, Bowlings <a href="https://www.artsy.net/show/tate-britain-frank-bowling-1">first major retrospective</a> opened at <a href="https://www.artsy.net/tate-britain">Tate Britain</a> in May.</p>',
