              type: "text",
              body:
                "<p>Frank Bowlings paintings are saturated with personal experiences and geo-political references, said Elena Crippa, Tates curator of modern and contemporary British art, who co-curated Bowlings retrospective with Laura Castagnini. From monumental expanses of color overlaid with stencilled outlines of maps to works produced stitching together fragments of canvas, they exude an extraordinary sense of open-endedness.</p>",
