              body:
                '<p>With titles like <em>Amazon </em>(1993) and <em>Antigone </em>(1970), <a href="https://www.artsy.net/artist/zilia-sanchez">Zilia Snchez</a>s three-dimensional paintings evoke woman warriors and ancient mythologies. Her works are minimal and metaphoric, but leave lasting impressions. </p>',
