                  caption:
                    "<p>Lorraine OGrady, <i>The Fir-Palm, </i>1991/2012. Courtesy of Alexander Gray Associates, New York.  Lorraine OGrady/Artists Rights Society (ARS), New York.</p>",
                  type: "image",
