              body:
                '<p><a href="https://www.artsy.net/artist/teresa-burga">Teresa Burga</a>s most iconic project, <em>Profile of the Peruvian Woman</em> (198081), was an investigation into the lives of middle-class Peruvian women through meticulous surveys. The artist went on to display the results through a series of objects, including a colorful indigenous yarn grid (or <em>quipu</em>). Her brightly painted, two-dimensional female bodies from 1967 appear playful, but are meant to parody ideals of femininity. Most impressive of Burgas practice is that she has been questioning technology and information in her art since the 1970s. A pioneer of installation, media, and technology-driven art, Burga was also a founding member of the 1960s avant-garde group Arte Nuevo, known for producing <a href="https://www.artsy.net/gene/happenings">Happenings</a>, <a href="https://www.artsy.net/gene/op-art">Op art</a>, and <a href="https://www.artsy.net/gene/pop-art">Pop art</a>. </p>',
