              type: "text",
              body:
                '<p><a href="https://www.artsy.net/artist/mcarthur-binion">McArthur Binion</a>s work beckons you to get closer. From afar, his canvases look like painted, gridded abstractions, while suggesting hanging, knitted tapestries. Walk towards one, though, and each individual cell takes on a distinct texture, with subtle color variations throughout the composition. Binions works are made of paper, crayon, laser-printed images, and oil paint sticks; he builds them from personal documents, including copies of his birth certificate and pages from his address book. His oeuvre might be seen as a body of abstracted self-portraits, rendered with data. </p>',
