              body:
                '<p>At 80 years old, painter and sculptor <a href="https://www.artsy.net/artist/beatriz-gonzalez">Beatriz Gonzlez</a> has been called one of the founders of modern Colombian art, but her international profile has grown in the past few years. Originally a figurative painter, Gonzlez changed styles in the 1960s, creating vivid, colorblocked compositions based on mass media. In 1965, her painting <em>The Suicides of the Sisga I, II and III </em>was refused by the jury of the Salon of Colombian Artists. The artist has often dealt with violent unrest in her work, having come of age during a bloody era of Colombian history known as <em>La Violencia</em>.</p>',
