              type: "text",
              body:
                '<p>In exile from her home in war-torn Lebanon, <a href="https://www.artsy.net/artist/simone-fattal">Simone Fattal</a> found grace in literature and visual art. She settled in California in 1980, started The Post-Apollo Press, and wrote and distributed experimental poetry and fiction. When not writing or publishing, Fattal has pursued an expansive art practice that includes painting, collage, and sculpture. She may be best known for her ceramics, which suggest truncated human figures and archeological ruinsforms that seem to echo the destruction and violence of her past. A love for narrative is evident in Fattals visual work. Ruba Katrib, curator of <a href="https://www.artsy.net/show/moma-ps1-simone-fattal-works-and-days">Works and Days</a>, the recently closed retrospective of Fattals work at <a href="https://www.artsy.net/moma-ps1">MoMA PS1</a>, remarked that the artist reconsiders how stories are told.</p>',
