              type: "text",
              body:
                '<p>Nearly 20 years after he emerged on the Los Angeles art scene by making paintings on empty cigarette boxes and selling them for $80, <a href="https://www.artsy.net/artist/henry-taylor">Henry Taylor</a> is making the best work of his life. In 2017, his large, ravishing portraitsof a black man grilling meat on his lawn; of the 32-year-old Philando Castile being shot by a Minnesota police officerwere a highlight of the Whitney Biennial in New York. Months later, his dealer <a href="https://www.artsy.net/galerie-eva-presenhuber">Eva Presenhuber</a> unveiled a raucous show of his portraits at her Zurich gallery, with paintings of the artists friends and neighbors, and of Andrea Motley Crabtree, the first female deep sea diver in the U.S. Army. (Blum &amp; Poe has been Taylors primary dealer for the past decade.)</p>',
