              type: "text",
              body:
                '<p>Maiolino has been an avant-garde force since the early 1960s, when she was aligned with the Brazilian New Figuration movement. Her prolific oeuvre includes personal drawings, woodblock prints, and cement sculptures, as well as political films and performances that share a lexicon of shapes, words, and symbols. She belongs to a generation of Latin American female artists who have begun to receive broader recognition in corrective survey shows like Radical Women: Latin American Art, 19601985, which originated at the <a href="https://www.artsy.net/hammer-museum">Hammer Museum</a> in 2017 before traveling to the <a href="https://www.artsy.net/brooklyn-museum">Brooklyn Museum</a> and So Paulos Pinacoteca. </p>',
